.TodoTemplate {
  width: 512px;
  // width가 주어진 상태에서 좌우 중앙 정렬
  margin-left: auto;
  margin-right: auto;
  margin-top: 6rem;
  border-radius: 4px;
  overflow: hidden;

  .app-title {
    padding: 10px 0;
    background: #22b8cf;
    color: white;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .date {
      font-size: 1.3rem;
    }

    h1 {
      margin: 0;
      padding: 10px 0 0;
      font-size: 1.5rem;
    }
  }

  .app-desc {
    padding: 10px;
    font-weight: bold;
    color: #22b8cf;
    text-align: right;
    background-color: white;
  }

  .content {
    background: white;
  }
}
